'use strict';

const bindModal = (triggerSelector, closeSelector, modalSelector = '.popup', addFormSelector = '.popup__form') => {
	const trigger = document.querySelector(triggerSelector);
	const modal = document.querySelector(modalSelector);
	const addForm = document.querySelector(addFormSelector);
	const addInput = addForm.querySelector('.popup__input');
	const tipPopup = document.querySelector('.tip-popup');
	const closeBtn = document.querySelector(closeSelector);

	const hideItem = (item) => {
		item.classList.add('hide');
		item.classList.remove('show');
	};

	const showItem = (item) => {
		item.classList.add('show');
		item.classList.remove('hide');
	};


	trigger.addEventListener('click', (event) => {
		event.preventDefault();
		const logoName = document.querySelector('.logo__name');

		logoName.style.opacity = '0.3';

		showItem(modal);
		document.body.style.overflow = 'hidden';
	});

	addForm.addEventListener('submit', (event) => {
		if (addInput.value) {
			event.preventDefault();

			hideItem(modal);
			showItem(tipPopup);
			setTimeout(function() {
				hideItem(tipPopup);
		}, 1500);
		document.body.style.overflow = 'visible';
		}

	});


	tipPopup.addEventListener('click', () => {
		console.log('click');
	});

	closeBtn.addEventListener('click', (event) => {
		hideItem(modal);

	});

	hideItem(modal);
	hideItem(tipPopup);
};

bindModal('.order-btn', '.close');



